.clicked {
  cursor: pointer;
  background-color: rgb(230, 217, 236);
  width: 100%;
  padding: 10px;
  color: rgb(0, 0, 0);
  display: block;
  font-family: 'Lato', sans-serif;
  outline: none;
  border: none;
  border-radius: 10px;

}